  font-family: 'Inter', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f7f6;
  color: #333;
}

.admin-page-layout {
  padding: 2rem;
  margin: 2rem auto;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(2, 1fr);
  /* 2 columns on larger screens */
}

.admin-page-container {
  padding: 20px;
  margin: 2rem;
  background-color: #ffffff;
  border-radius: 12px;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.08);
  box-sizing: border-box;
}

.admin-page-header h1 {
  font-size: 2.5em;
  color: #2c3e50;
  margin-bottom: 10px;
  font-weight: 700;
}

.admin-page-header p {
  font-size: 1.1em;
  color: #555;
  margin-bottom: 30px;
  border-bottom: 1px solid #eee;
  padding-bottom: 20px;
}

.metadata-section {
  background-color: #eaf6fd;
  border: 1px solid #c7e0f0;
  border-radius: 8px;
  padding: 20px;
  height: 100%;
  margin-bottom: 30px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
}

.metadata-section h2 {
  font-size: 1.8em;
  color: #2c3e50;
  margin-bottom: 15px;
  border-bottom: 1px solid #d0e0ec;
  padding-bottom: 10px;
}

.metadata-item {
  margin-bottom: 2rem;
}

.metadata-item p {
  opacity: 0.6;
}

.metadata-item strong {
  display: inline-block;
  width: 180px;
