  color: #34495e;
  font-weight: 600;
  font-size: 1.2rem
}

.metadata-item span {
  color: #666;
  word-break: break-all;
  font-size: 1.2rem;
  /* Ensures long strings wrap */
}

.metadata-item ul {
  list-style: none;
