}

.metadata-item ul li {
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  padding: 5px 10px;
  margin-bottom: 3px;
  border-radius: 4px;
  font-size: 0.9em;
  display: inline-block;
  /* For inline display of list items */
  margin-right: 5px;
}

.table-section {
  margin-top: 30px;
}

/* Styles for fixed height and scrollable table content */
.table-wrapper-scroll {
  max-height: 30rem;
  /* Fixed height for your table container */
  overflow-y: auto;
  /* Enables vertical scrolling when content exceeds max-height */
  border: 1px solid #e0e0e0;
  /* Optional: adds a border around the scrollable area */
  border-radius: 0.25rem;
  margin-top: 1rem;
  /* Spacing from elements above */
  background-color: #ffffff;
}

.data-table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  border-radius: 8px;
  overflow: hidden;
  /* Ensures rounded corners apply to content */
}

.data-table th,
.data-table td {
  padding: 12px 15px;
  text-align: left;
  border-bottom: 1px solid #e0e0e0;
}

.data-table th {
  background-color: #3498db;
  color: white;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 0.9em;
}

.data-table tr:nth-child(even) {
  background-color: #f9f9f9;
}

.data-table tr:hover {
  background-color: #f0f8ff;
  cursor: pointer;
}

.pagination-controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 20px;
  padding: 10px 0;
  border-top: 1px solid #e0e0e0;
}

.pagination-controls button {
  background-color: #3498db;
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9em;
  transition: background-color 0.3s ease;
}

.pagination-controls button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}

.pagination-controls button:hover:not(:disabled) {
  background-color: #2980b9;
}

.pagination-controls span {
  font-size: 0.9em;
  color: #666;
}

.rows-per-page select {
  padding: 8px 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: white;
  cursor: pointer;
}

.add-row-form {
  margin-top: 40px;
  background-color: #f9f9f9;
  padding: 25px;
  border-radius: 10px;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.05);
}

.add-row-form h2 {
  font-size: 1.8em;
  color: #2c3e50;
  margin-bottom: 20px;
  border-bottom: 1px solid #eee;
  padding-bottom: 10px;
}

.tab-buttons {
  display: flex;
  margin-bottom: 20px;
  border-bottom: 1px solid #eee;
}

.tab-button {
  flex: 1;
  padding: 10px 15px;
  border: none;
  background-color: #e0e0e0;
  color: #555;
  cursor: pointer;
  font-size: 1em;
  font-weight: 600;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.tab-button:not(:last-child) {
  margin-right: 5px;
}

.tab-button.active {
  background-color: #3498db;
  color: white;
  box-shadow: 0 -2px 8px rgba(0, 0, 0, 0.1);
}

.single-row-form-content,
.bulk-add-form-content {
  padding-top: 10px;
}

.form-group {
  margin-bottom: 15px;
}

.form-group label {
  display: block;
  margin-bottom: 5px;
  font-weight: 600;
  color: #444;
  font-size: 0.95em;
}

.form-group input,
.form-group textarea {
  width: calc(100% - 20px);
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 1em;
  box-sizing: border-box;
  resize: vertical;
}

.helper-text {
  font-size: 0.85em;
  color: #777;
  margin-top: 5px;
  line-height: 1.4;
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 5px;
  border: 1px dashed #ccc;
}

.helper-text code {
  background-color: #e9e9e9;
  padding: 2px 5px;
  border-radius: 3px;
  font-family: monospace;
}

.add-row-form button {
  background-color: #28a745;
  color: white;
  border: none;
  padding: 12px 25px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1.1em;
  font-weight: 600;
  transition: background-color 0.3s ease;
  margin-top: 15px;
  box-shadow: 0 4px 10px rgba(40, 167, 69, 0.2);
}

.add-row-form button:hover {
  background-color: #218838;
}

.confirmation-banner {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  padding: 30px;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  text-align: center;
  width: 90%;
  max-width: 450px;
  border: 1px solid #ccc;
}

.confirmation-banner h3 {
  font-size: 1.6em;
  color: #2c3e50;
  margin-bottom: 20px;
}

.confirmation-banner p {
  font-size: 1.1em;
  color: #555;
  margin-bottom: 25px;
}

.confirmation-buttons button {
  margin: 0 10px;
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1em;
  font-weight: 600;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.confirmation-buttons .confirm-button {
  background-color: #3498db;
  color: white;
}

.confirmation-buttons .confirm-button:hover {
  background-color: #2980b9;
  transform: translateY(-2px);
}

.confirmation-buttons .cancel-button {
  background-color: #e74c3c;
  color: white;
}

.confirmation-buttons .cancel-button:hover {
  background-color: #c0392b;
  transform: translateY(-2px);
}

/* Overlay for the banner */
.overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 999;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .admin-page-layout {
    grid-template-columns: 1fr;
    /* Single column layout */
  }

  .admin-page-container {
    padding: 15px;
    margin: 10px auto;
  }

  .admin-page-header h1 {
    font-size: 2em;
  }

  .admin-page-header p {
    font-size: 1em;
  }

  .metadata-item strong {
    width: 150px;
  }

  .metadata-item ul {
    padding-left: 155px;
  }

  .data-table th,
  .data-table td {
    padding: 8px 10px;
    font-size: 0.85em;
  }

  .pagination-controls {
    flex-direction: column;
    gap: 10px;
  }

  .add-row-form {
    padding: 20px;
  }

  .form-group input,
  .form-group textarea {
    width: 100%;
  }

  .confirmation-banner {
    padding: 20px;
  }
}

@media (max-width: 480px) {
  .admin-page-container {
    padding: 10px;
  }

  .admin-page-header h1 {
    font-size: 1.8em;
  }

  .metadata-item {
    display: flex;
    flex-direction: column;
    margin-bottom: 15px;
    border-bottom: 1px dashed #eee;
    padding-bottom: 10px;
  }

  .metadata-item strong {
    width: 100%;
    margin-bottom: 5px;
  }

  .metadata-item ul {
    padding-left: 0;
  }

  .metadata-item ul li {
    display: block;
    /* Stack list items vertically */
    margin-right: 0;
    margin-bottom: 3px;
  }

  .data-table thead {
    display: none;
    /* Hide headers on small screens */
  }

  .data-table,
  .data-table tbody,
  .data-table tr,
  .data-table td {
    display: block;
    width: 100%;
  }

  .data-table tr {
    margin-bottom: 10px;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    overflow: hidden;
  }

  .data-table td {
    text-align: right;
    padding-left: 50%;
    position: relative;
  }

  .data-table td::before {
    content: attr(data-label);
    position: absolute;
    left: 10px;
    width: calc(50% - 20px);
    text-align: left;
    font-weight: 600;
    color: #555;
  }

  .pagination-controls button {
    width: 100%;
  }

  .rows-per-page {
    width: 100%;
    text-align: center;
  }

  .rows-per-page select {
    width: calc(100% - 20px);
  }

  .tab-button {
    border-radius: 0;
    /* Remove rounded corners for small screens */
  }

  .tab-button:first-child {
    border-top-left-radius: 8px;
  }

  .tab-button:last-child {
    border-top-right-radius: 8px;
  }

  .confirmation-buttons {
    flex-direction: column;
    gap: 10px;
  }

  .confirmation-buttons button {
    width: calc(100% - 20px);
  }
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #f3f4f6;
  padding: 1rem;
  font-family: 'Inter', sans-serif;
}

.card {
  background-color: #fff;
  padding: 2rem;
  border-radius: 0.5rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  text-align: center;
  max-width: 28rem;
  width: 100%;
}

.title {
  font-size: 1.5rem;
  font-weight: 700;
  color: #dc2626;
  margin-bottom: 1rem;
}

.message {
  color: #1f2937;
  font-size: 1.125rem;
}

.icon-container {
  margin-top: 1.5rem;
}

.icon {
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 3rem;
  width: 3rem;
  color: #ef4444;
